'use strict'

import { API } from '../modules/api'

export interface Store {
	store_id: string
	name: string
	card_img: string
	rating: number | string
	description?: string
	tags?: string[]
	delivery_time?: string
}

export interface GetStoresParams {
	limit?: number
	lastId?: string
	tagId?: string
	sorted?: string
	desc?: boolean
	search?: string
	category?: string
}

export interface StoresResponse {
	stores: Store[]
	total?: number
	hasMore?: boolean
	lastId?: string
}

export class StoreApi {
	/**
	 * –ü–æ–ª—É—á–∏—Ç—å —Å–ø–∏—Å–æ–∫ –º–∞–≥–∞–∑–∏–Ω–æ–≤
	 * @param {GetStoresParams} params - –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –∑–∞–ø—Ä–æ—Å–∞
	 * @returns {Promise<Store[]>} - —Å–ø–∏—Å–æ–∫ –º–∞–≥–∞–∑–∏–Ω–æ–≤
	 */
	static async getStores(params: GetStoresParams = {}): Promise<Store[]> {
		const body = {
			limit: params.limit || 12,
			last_id: params.lastId || '',
			tag_id: params.tagId || '',
			sorted: params.sorted || '',
			desc: params.desc || false,
			search: params.search || '',
			category: params.category || '',
		}

		console.log('üöÄ StoreApi: Making request to /stores')
		console.log('üöÄ StoreApi: Request body:', body)

		const response = await API.fetch('/stores', {
			method: 'POST',
			body: JSON.stringify(body),
		})

		console.log('üöÄ StoreApi: Response status:', response.status)
		console.log('üöÄ StoreApi: Response ok:', response.ok)

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		const data = await response.json()
		console.log('üöÄ StoreApi: Response data received:', data)

		if (Array.isArray(data)) {
			return data
		} else if (data && Array.isArray(data.stores)) {
			return data.stores
		} else {
			console.warn('StoreApi: Unexpected API response format:', data)
			return []
		}
	}

	/**
	 * –ü–æ–ª—É—á–∏—Ç—å –º–∞–≥–∞–∑–∏–Ω –ø–æ ID
	 * @param {string} storeId - ID –º–∞–≥–∞–∑–∏–Ω–∞
	 * @returns {Promise<Store | null>} - –º–∞–≥–∞–∑–∏–Ω
	 */
	static async getStoreById(storeId: string): Promise<Store | null> {
		const response = await API.fetch(`/stores/${storeId}`, {
			method: 'GET',
		})

		if (!response.ok) {
			throw new Error(`HTTP error! status: ${response.status}`)
		}

		return await response.json()
	}

	/**
	 * –ü–æ–∏—Å–∫ –º–∞–≥–∞–∑–∏–Ω–æ–≤
	 * @param {string} query - –ø–æ–∏—Å–∫–æ–≤—ã–π –∑–∞–ø—Ä–æ—Å
	 * @param {number} limit - –ª–∏–º–∏—Ç —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤
	 * @returns {Promise<Store[]>} - —Å–ø–∏—Å–æ–∫ –º–∞–≥–∞–∑–∏–Ω–æ–≤
	 */
	static async searchStores(
		query: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ search: query, limit })
	}

	/**
	 * –ü–æ–ª—É—á–∏—Ç—å –º–∞–≥–∞–∑–∏–Ω—ã –ø–æ –∫–∞—Ç–µ–≥–æ—Ä–∏–∏
	 * @param {string} category - –∫–∞—Ç–µ–≥–æ—Ä–∏—è
	 * @param {number} limit - –ª–∏–º–∏—Ç —Ä–µ–∑—É–ª—å—Ç–∞—Ç–æ–≤
	 * @returns {Promise<Store[]>} - —Å–ø–∏—Å–æ–∫ –º–∞–≥–∞–∑–∏–Ω–æ–≤
	 */
	static async getStoresByCategory(
		category: string,
		limit: number = 12,
	): Promise<Store[]> {
		return this.getStores({ category, limit })
	}
}
